<template>
  <panel>
    <div slot="header">
      <el-button @click="handleAdd" type="primary"></el-button>
    </div>

    <el-table :data="dataList" border>
      <el-table-column label="ID" prop="id"></el-table-column>
      <el-table-column label="" prop="name"></el-table-column>
      <el-table-column label="" prop="articleCount"></el-table-column>
      <el-table-column label="" prop="articlePublishedCount"></el-table-column>
      <el-table-column label="">
        <template v-slot="{row}">{{new Date(row.createdAt).toLocaleString()}}</template>
      </el-table-column>
      <el-table-column label="">
        <template v-slot="{row,$index}">
          <el-button @click="editOne(row)" type="primary" size="small"></el-button>
          <el-button @click="deleteOne(row.id,$index)" type="danger" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog @opened="onDialogOpen" :visible="dialogVisible" @close="closeDialog" :title="dialogTitle">
      <el-form label-width="auto">
        <el-form-item label="">
          <el-input ref="input" v-model="form.name" clearable></el-input>
        </el-form-item>
      </el-form>
      <el-button slot="footer" @click="handleSubmit"></el-button>
    </el-dialog>
  </panel>
</template>

<script>
import ArticleCategoryService from '@/api/categories'

export default {
  data() {
    return {
      form: {
        name: ''
      },
      dialogVisible: false,
      dataList: [],
      action: ''
    }
  },
  created() {
    ArticleCategoryService.fetchList()
      .then(response => {
        this.dataList = response
      })
      .catch(err => {
        this.$message.error(err.message)
      })
  },
  computed: {
    dialogTitle() {
      return this.action === 'create' ? '' : ''
    }
  },
  methods: {
    onDialogOpen() {
      this.$refs.input.focus()
    },
    openDialog() {
      this.dialogVisible = true
    },
    closeDialog() {
      this.dialogVisible = false
      this.resetDialog()
    },
    resetDialog() {
      this.form.name = ''
    },
    handleAdd() {
      this.action = 'create'
      this.openDialog()
    },
    async deleteOne(id, index) {
      try {
        await this.$confirm('?', '', {
          type: 'warning'
        })
      } catch (error) {
        return
      }

      ArticleCategoryService.deleteOneWithId(id)
        .then(() => {
          this.dataList.splice(index, 1)
          this.$message.success('')
        })
        .catch(err => {
          this.$message.error(err.message)
        })
    },
    editOne(item) {
      this.action = 'edit'
      this.editingItem = item
      _.assign(this.form, _.pick(item, _.keys(this.form)))
      this.openDialog()
    },
    handleSubmit() {
      const { name } = this.form

      if (!name) {
        this.$message.error('')
        return
      }

      const payload = { name }

      const action =
        this.action === 'create'
          ? ArticleCategoryService.createOne(payload)
          : ArticleCategoryService.updateOneWithId(this.editingItem.id, payload)

      action
        .then(response => {
          if (this.action === 'create') {
            this.dataList.unshift(response)
          } else {
            _.assign(this.editingItem, payload)
          }

          this.$message.success(`${this.dialogTitle}`)
          this.closeDialog()
        })
        .catch(err => {
          this.closeDialog()
          this.$message.error(err.message)
        })
    }
  }
}
</script>

