<template>
  <div class>
    <el-tabs @tab-click="toggleTab" v-model="activeName">
      <el-tab-pane name="list" label="">
        <el-table :data="dataList" border>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="articleCount" label=""></el-table-column>
          <el-table-column prop="articlePublishedCount" label=""></el-table-column>
          <el-table-column label="">
            <template v-slot="{row}">{{new Date(row.updatedAt).toLocaleTimeString()}}</template>
          </el-table-column>
          <el-table-column label="">
            <template v-slot="{row,$index}">
              <el-button type="primary" @click="editOne(row.id,row)" size="small"></el-button>
              <el-button type="danger" @click="deleteOne(row.id,$index)" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane name="action" :label="tagAction">
        <el-form>
          <el-form-item label="">
            <el-input ref="input" v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSubmit"></el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import TagApi from '@/api/tags'
export default {
  name: 'Tag',
  data() {
    return {
      dataList: [],
      activeName: 'list',
      action: 'create',
      form: {
        name: ''
      }
    }
  },
  computed: {
    tagAction() {
      return this.action === 'edit' ? '' : ''
    }
  },
  watch: {
    activeName() {
      setTimeout(this.$refs.input.focus)
    }
  },
  created() {
    TagApi.fetchList()
      .then(response => {
        this.dataList = response
      })
      .catch(err => this.$message.error(err.message))
  },
  methods: {
    handleSubmit() {
      const { name } = this.form
      if (!name) {
        this.$message.error('')
        return
      }

      const action =
        this.action === 'edit'
          ? TagApi.updateOne(this.editingItem.id, { name })
          : TagApi.createOne({ name })

      action
        .then(response => {
          if (this.action === 'edit') {
            this.editingItem.name = name
            this.$message.success('')
          } else {
            this.$message.success('')
            this.dataList.unshift(response)
          }
          this.resetTab()
        })
        .catch(err => this.$message.error(err.message))
    },
    resetTab() {
      this.activeName = 'list'
      this.action = 'create'
      this.form.name = ''
    },
    toggleTab(activeInstance) {
      if (activeInstance.name === 'list') {
        this.resetTab()
      }
    },
    async deleteOne(id, index) {
      try {
        await this.$confirm(',  ?', '', {
          type: 'warning'
        })
      } catch (err) {
        return
      }

      TagApi.deleteOne(id)
        .then(() => {
          this.$message.success('')
          this.dataList.splice(index, 1)
        })
        .catch(err => this.$message.error(err.message))
    },
    editOne(id, row) {
      this.editingItem = row
      this.activeName = 'action'
      this.action = 'edit'
      _.assign(this.form, _.pick(row, ['name']))
    }
  }
}
</script>
